.card-menu{
  min-height: 170px;
  background-color: #fbf5f3;
  border-radius: 5px;
  width: 140px;

}

.newMenu-Data__type {
  padding: 5px 18px 5px 18px;
  text-transform: uppercase;
  background-color: whitesmoke;
  border: whitesmoke;
  border-radius: 5px;
  font-family: 'Voltaire', sans-serif;
  text-align: center;
  font-size: 18px;
  transition: .3s;
  border: solid 1px whitesmoke;

}

.newMenu-Data__type:hover {
  background-color:black;
  
}


.newMenu-Data__item {
  display: inline-block;
  font-family: 'Voltaire', sans-serif;
  cursor: pointer;
  text-align: center;
}

.newMenu-Data__item span {
  display: block;
}

.card-menu:hover {
  background-color:lightgrey;
  opacity:90%;
  border:black;
  transform: scale(1.1);
  transition: .3s;
}

